/* üîπ ÿ™ÿ®ŸàŸäÿ® ÿßŸÑÿ¥ŸÅÿ™ÿßÿ™ */
function ShiftsTab({ shifts, formatCurrency }) {
  return (
    <div className="space-y-4">
      <h2 className="text-lg font-semibold text-gray-800">
        üïí ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿ¥ŸÅÿ™ÿßÿ™
      </h2>
      <div className="p-4 overflow-x-auto bg-white border rounded-lg shadow-sm">
        <table className="w-full text-sm text-right min-w-[900px]">
          <thead className="text-gray-600 bg-gray-50">
            <tr>
              <th className="px-3 py-2">ÿ±ŸÇŸÖ ÿßŸÑÿ¥ŸÅÿ™</th>
              <th className="px-3 py-2">ÿßŸÑŸÉÿßÿ¥Ÿäÿ±</th>
              <th className="px-3 py-2">ŸàŸÇÿ™ ÿßŸÑŸÅÿ™ÿ≠</th>
              <th className="px-3 py-2">ŸàŸÇÿ™ ÿßŸÑÿ•ÿ∫ŸÑÿßŸÇ</th>
              <th className="px-3 py-2">ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™</th>
              <th className="px-3 py-2">ŸÜŸÇÿØŸãÿß</th>
              <th className="px-3 py-2">ÿ®ÿ∑ÿßŸÇÿ©</th>
              <th className="px-3 py-2">ŸÖÿ≠ŸÅÿ∏ÿ©</th>
              <th className="px-3 py-2">ÿπÿØÿØ ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ±</th>
            </tr>
          </thead>
          <tbody>
            {shifts.map((s) => (
              <tr key={s.id} className="border-t hover:bg-gray-50">
                <td className="px-3 py-2">{s.id}</td>
                <td className="px-3 py-2">{s.cashier}</td>
                <td className="px-3 py-2">
                  <SafeDate value={s.openedAt} />
                </td>
                <td className="px-3 py-2">
                  {s.closedAt ? (
                    <SafeDate value={s.closedAt} />
                  ) : (
                    "ŸÖŸÅÿ™Ÿàÿ≠"
                  )}
                </td>
                <td className="px-3 py-2">
                  {formatCurrency(s.totals.totalSales)}
                </td>
                <td className="px-3 py-2">
                  {formatCurrency(s.totals.totalCash)}
                </td>
                <td className="px-3 py-2">
                  {formatCurrency(s.totals.totalCard)}
                </td>
                <td className="px-3 py-2">
                  {formatCurrency(s.totals.totalWallet)}
                </td>
                <td className="px-3 py-2">
                  {s.totals.invoiceCount}
                </td>
              </tr>
            ))}
            {!shifts.length && (
              <tr>
                <td colSpan="9" className="py-4 text-center text-gray-500">
                  ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ¥ŸÅÿ™ÿßÿ™ ŸÖÿ≥ÿ¨ŸÑÿ© ÿ®ÿπÿØ‚Ä¶
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}

/* üîπ ŸÉÿ±ÿ™ ŸÖŸÑÿÆÿµ */
function SummaryCard({ title, value, color }) {
  return (
    <div className="p-4 text-center bg-white border rounded-lg shadow-sm">
      <p className="text-xs text-gray-500">{title}</p>
      <p className={`mt-1 text-xl font-bold ${color}`}>{value}</p>
    </div>
  );
}
















